<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid p-1">
        <p class="fs-5 my-2 text-start ps-2">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!freeSessions">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else class="row g-0">
          <template v-for="item in freeSessions" :key="item.id">
            <div class="col-6 col-sm-12">
              <div
                class="row g-0 bg-light m-1 m-sm-2 align-items-center rounded shadow"
              >
                <div class="col-12 col-sm-1">
                  <img
                    class="w-100"
                    :src="API_STORAGE_URL + item.product.thumbnail"
                    @error="noPic"
                  />
                </div>
                <div
                  class="col-12 my-2 col-sm-4 fs-6 fw-bold text-decoration-none"
                >
                  جلسه {{ item.numName }}
                </div>
                <div class="col-12 my-2 col-sm-5 text-decoration-none">
                  تاریخ برگزاری:
                  <span>
                    {{
                      jmoment(item.videoSession.start_date).format(
                        "jYYYY/jMM/jDD"
                      )
                    }}
                    ,ساعت
                    {{ item.videoSession.start_time }} الی
                    {{ item.videoSession.end_time }}
                  </span>
                </div>
                <div class="col-12 col-sm-2 mb-2 mb-sm-0">
                  <router-link
                    class="btn btn-sm btn-theme d-block d-sm-inline"
                    :to="{
                      name: 'VideoSession',
                      params: {
                        videoSessionId: item.id,
                        productId: item.product.id,
                      },
                    }"
                  >
                    <i class="fas fa-check me-1"></i>
                    <span>ورود رایگان</span>
                  </router-link>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";
import jmoment from "moment-jalaali";

export default {
  name: "SingleSessions",
  created() {
    this.arefApi = new ArefApi();
    this.utils = new utils();
    this.jmoment = jmoment;
    this.getFreeSessions();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      freeSessions: null,
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
    };
  },
  methods: {
    getFreeSessions() {
      this.arefApi
        .getFreeSessions()
        .then((resp) => {
          let tmpFreeSessions = resp.data;
          tmpFreeSessions.sort((a,b)=>{
            if(a.videoSession.start_date !==b.videoSession.start_date){
              return a.videoSession.start_date > b.videoSession.start_date ? -1:1;
            }
            else{
              return a.videoSession.start_time > b.videoSession.start_time ? 1:-1;
            }
          })
          this.freeSessions = tmpFreeSessions;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    noPic(e){
      e.target.src = process.env.VUE_APP_API_STORAGE_URL + process.env.VUE_APP_NO_IMAGE_URL
    }
  },
};
</script>

